  .Navigation-trigger .Cell {
    text-align: center;
  }
}
.Navigation-trigger__button {
  background: transparent;
  border: none;
  cursor: pointer;
  font-size: calc(12px + 0 * ((100vw - 420px) / 860));
  padding: 0 0 2em 0;
  position: relative;
}
@media screen and (min-width: 1280px) {
  .Navigation-trigger__button {
    font-size: 12px;
  }
}
@media screen and (max-width: 420px) {
  .Navigation-trigger__button {
    font-size: 12px;
  }
}
.Navigation-trigger__button:before,
.Navigation-trigger__button:after {
  background-color: #555350;
  content: '';
  display: block;
  height: 5px;
  left: 0;
  position: absolute;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  top: 25px;
  width: 100%;
}
.Navigation-trigger__button:after {
  top: 35px;
}
.Navigation-trigger__button.is-visible {
  color: #ff7f11;
}
.Navigation-trigger__button.is-visible:before,
.Navigation-trigger__button.is-visible:after {
  background-color: #ff7f11;
  height: 2px;
}
.Navigation-trigger__button.is-visible:before {
  top: 35px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.Navigation-trigger__button.is-visible:after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.Navigation {
